@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.textbullet {
  width: 100%;
  margin-left: auto;
  &__title {
    font-size: 56px;
    letter-spacing: 0.1em;
    width: 40%;
    display: block;
    margin-left: auto;
    @include media($media-mobile...) {
      font-size: 32px;
      width: 80%;
    }
  }
  &__content {
    width: 40%;
    font-size: 20px;
    line-height: 2;
    margin-left: auto;
    letter-spacing: 0.1em;
    @include media($media-mobile...) {
      font-size: 16px;
      width: 80%;
    }
  }
  ol {
    list-style: none;
    counter-reset: my-awesome-counter;
    padding-left: 2em;
  }
  ol li {
    counter-increment: my-awesome-counter;
  }
  ol li::before {
    content: counter(my-awesome-counter);
    color: #222;
    background-color: #C41F1F;
    width: 1.4em;
    height: 1.4em;
    border-radius: 50%;
    font-weight: 700;
    display: inline-block;
    text-align: center;
    line-height: 1.4em;
    margin-left: -2em;
    margin-right: 0.6em;
  }
}