@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.texttable {
    &__content {
        width: 60%;
        display: flex;
        flex-wrap: wrap;
        @include media($media-mobile...) {
          width: 100%;
        }
    }
    &__title {
      font-size: 56px;
      letter-spacing: 0.1em;
      width: 60%;
      @include media($media-mobile...) {
        font-size: 32px;
        width: 100%;
      }
    }
    &__item {
      margin-bottom: 10%;
      &:nth-child(odd) {
        padding-right: 10px;
        width: calc(50% - 10px);
        @include media($media-mobile...) {
          width: 100%;
        }
      }
      &:nth-child(even) {
        padding-left: 10px;
        width: calc(50% - 10px);
        @include media($media-mobile...) {
          width: 100%;
          padding-left: 0;
        }
      }
      
    }
    &__number {
      font-size: 10vw;
      color: #C41F1F;
      font-weight: 700;
      span {
        font-size: 40px;
      }
    }
    &__desc {
      font-size: 20px;
      line-height: 2;
      letter-spacing: 0.1em;
      max-width: 400px;
      @include media($media-mobile...) {
        font-size: 16px;
      }
    }
  }