@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.photo {
  img {
    width: 100%;
  }
  &__small {
    // width: 100%;
    // padding-left: 0 !important;
    // padding-right: 0 !important;
    .photo__image {
      width: calc(60% + 70px);
      margin-left: -70px;
      padding-top: calc(39.96% + 46.2px);
      background-size: contain;
      background-repeat: no-repeat;
      @include media($media-mobile...) {
        width: 100%;
        margin-left: 0;
        margin-right: 0;
        padding-top: 66.6%;
      }
    }
  }
  &__full {
    .photo__image {
      width: 100%;
      padding-top: 66.6%;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
}