@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.horiscroll {
    width: 200%;
    height: 100%;
    display: flex;
    flex-wrap: nowrap;
    margin-left: 50%;
    align-items: flex-end;
    margin-top: -10vh;
    &__wrapper {
        height: 100%;
        width: 100%;
        overflow-x: hidden;
        @include media($media-mobile...) {
          padding: 0;
          overflow: visible;
          height: auto;
        }
    }
    @include media($media-mobile...) {
      margin-top: 10%;
      display: block;
      margin-left: 0;
      height: auto;
      padding: 0;
      width: 100%;
    }
  }
  
  .panel {
    width: 50%;
    padding-top: 16.6%;
    height: 0;
    font-weight: 700;
    font-size: 1.5em;
    text-align: center;
    color: white;
    position: relative;
    box-sizing: border-box;
    
    background-color: #222222;
    background-size: cover;
    background-position: center;
    @include media($media-mobile...) {
      padding-top: 66.6%;
      width: 100%;
    }
  }