@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.leaflet-container {
  height: 100vh;
  width: 100%;
  position: sticky !important;
  top: 0px;
  left: 0px;
}

.leaflet-control-container {
  display: none;
}

.intermap {
  padding: 0;
  height: 250vh;
  &__desc {
    margin-top: -100vh;
    width: 100%;
    position: sticky;
    right: 0;
    top: 0;
    z-index: 500;
    
    padding-right: 70px;
    box-sizing: border-box;
    text-align: right;
    margin-bottom: 40vh;
    padding-top: 70px;
    span {
      font-size: 40px;
      font-weight: 700;
      letter-spacing: 0.1em;
      display: block;
    }
    strong {
      font-size: 100px;
    }
    br {
      display: none;
    }
    @include media($media-mobile...) {
      padding-right: 25px;
      width: calc(45% - 10px);
      right: 0;
      margin-left: auto;
      padding-top: 30px;
      span {
        font-size: 16px;
      }
      strong {
        font-size: 53px;
      }
      br {
        display: block;
      }
    }
  }
  &__events {
    z-index: 2;
    position: sticky;
    text-align: left;
    top: 0;
    z-index: 500;
    font-size: 20px;
    line-height: 2;
    width: calc(40% - 56px);
    letter-spacing: 0.1em;
    position: sticky;
    left: 0;
    z-index: 500;
    box-sizing: border-box;
    padding-left: 70px;
    padding-top: 70px;
    span {
      display: block;
      line-height: 1.5;
    }
    strong {
      line-height: 1.5;
      display: block;
      margin-bottom: 1em;
      margin-top: 0.3em;
    }
    @include media($media-mobile...) {
      font-size: 14px;
      width: calc(53% - 10px);
      padding-left: 25px;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;;
    top: 0;
  }
}