@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.piechart {
  width: 80%;
  max-width: 800px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  @include media($media-mobile...) {
    width: 80%;
  }
  .chart__title {
    width: 100%;
    text-align: left;
  }
  svg {
    max-width: 800px;
  }
  .remark {
    text-align: left;
    margin-bottom: 10%;
  }
}