@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.ending {
    height: auto;
    position: relative;
    opacity: 1;
    min-height: 100vh;
    &.cover__chart {
        display: block;
    }
    .cover__chartnumber {
        font-size: 4.5vw;
    }
    .cover__chart__b {
        height: auto;
        &:first-child {
            margin-bottom: 3%;
        }
        &:nth-child(2) {
            min-height: 12.5vw;
            .cover__chartmark.middle {
                color: #6DB76D;
                
            }
            .cover__chartmark.bottom {
                color: #A1A1A1;
            }
        }
    }
    .cover__chartmark {
        @include media($media-mobile...) {
            margin-top: 10px;
          }
    }
    svg {
        max-width: 100vh;
        @include media($media-mobile...) {
            height: auto;
          }
    }
    &__credit {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        background-color: rgba(196,31,31,0.95);
        left: 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        h3 {
            width: 80%;
        }
        ul {
            width: 80%;
            list-style: none;
            padding-left: 0;
            li {
                text-align: left;
            }
            span {
                display: inline-block;
                width: calc(100% - 120px);
                vertical-align: top;
                margin-bottom: 5px;
            }
            span:first-child {
                font-weight: 700;
                width: 100px;
            }
        }
    }
    &__wrapper {
        height: 120vh;
    }
}

