@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.text {
  &__highlight {
    font-size: 40px;
    line-height: 1.5;
    letter-spacing: 0.16em;
    width: 70%;
    text-align: justify;
    @include media($media-mobile...) {
      font-size: 20px;
      width: 100%;
    }
  }
  &__content {
    font-size: 20px;
    line-height: 2;
    width: 40%;
    margin-left: auto;
    letter-spacing: 0.1em;
    @include media($media-mobile...) {
      font-size: 16px;
      width: 80%;
    }
  }
  &__center {
    font-size: 20px;
    line-height: 2;
    width: 40%;
    margin-left: auto;
    margin-right: auto;
    letter-spacing: 0.1em;
    @include media($media-mobile...) {
      font-size: 16px;
      width: 80%;
    }
  }
}