@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.textbanner {
  height: 50vh;
  background-color: #C41F1F;
  display: flex;
  justify-content: center;
  align-items: center;
  &__number {
    font-size: 10vw;
    color: #222;
    text-align: center;
    font-weight: 700;
    span {
      font-size: 40px;
      font-weight: 700;
      @include media($media-mobile...) {
        font-size: 20px;
      }
    }
    @include media($media-mobile...) {
      font-size: 20vw;
    }
  }
  &__desc {
    font-size: 20px;
    line-height: 2;
    margin-left: auto;
    margin-right: auto;
    letter-spacing: 0.1em;
    width: 80%;
    max-width: 500px;
    @include media($media-mobile...) {
      font-size: 16px;
    }
  }
}