@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.voice {
  display: flex;
  justify-content: flex-end;
  button {
    left: 0;
    width: 120px;
    height: 60px;
    background-color: #C41F1F;
    border: none;
    outline: none;
    border-radius: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 15px;
    margin-top: 20px;
    color: #fff;
    &:hover {
      cursor: pointer;
    }
    @include media($media-mobile...) {
      width: 80px;
      height: 40px;
      margin-top: 10px;
      font-size: 12px;
      padding: 0 10px;
    }
  }
  &__waveform {
    width: 40%;
    height: 100px;
    @include media($media-mobile...) {
      width: calc(100% - 80px);
      height: 60px;
    }
    wave {
      height: 100% !important;
    }
  }
}

