@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.photogallery {
  width: 100%;
  box-sizing: border-box;
  height: 250vh;
  overflow: hidden;
  padding-top: 50vh;
  margin-bottom: -20vh;
  &__image {
    width: 100%;
    height: 50%;
    position: relative;
  }
  .thumbnail {
    position: absolute;
    width: calc(16vw + 16vw);
    z-index: -1;
    left: calc(42vw - 16vh);
    top: calc(50% - 9.4vw);
    padding: 1% 3%;
    background-color: #ddd;
    @include media($media-mobile...) {
      width: 50%;
    }
  }
  &__title {
    z-index: -1;
    font-size: 4vw;
    text-align: center;
    color: #fff;
    background-color: #C41F1F;
    width: 55%;
    left: 22.5%;
    top: 40%;
    position: absolute;
    padding: 2% 0;
    transform: translateY(-50%);
    font-weight: 700;
    @include media($media-mobile...) {
      height: 90vw;
      top: 40%;
      width: 25%;
      left: 37.5%;
      writing-mode: vertical-rl;
      font-size: 6vw;
      line-height: 2;
      padding: 0
    }
  }
}