@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.textbg {
  background-size: 100% auto;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding-bottom: 80px;
  padding-top: 50vh;
  background-repeat: no-repeat;
  background-color: #000;
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(196, 31, 31, 0.6);
    top: 0;
    left: 0;
  }
  &__wrapper {
    width: 40%;
    @include media($media-mobile...) {
      width: 80%;
    }
  }
  &__item {
    font-size: 20px;
    line-height: 2;
    letter-spacing: 0.1em;
    width: 100%;
    margin-bottom: 1em;
    &:before {
      content: "";
      position: absolute;
      height: 20px;
      background-color: #222;
      width: 100%;
      left: 0;
      margin-top: 10px;
      z-index: 1;
    }
    span {
      z-index: 1;
      position: relative;
    }
    @include media($media-mobile...) {
      font-size: 16px;
    }
  }
  @include media($media-mobile...) {
    padding-bottom: 40px;
    padding-top: 30vh;
  }
}