@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.bar {
    fill: #C41F1F;
}

.axis {
    font-size: 13px;
}

.axis path,
.axis line {
    fill: none;
    display: none;
}

.label {
    font-size: 13px;
}

#chartBMobile .text-label, #chartB .text-label {
    
    @include media($media-mobile...) {
        font-size: 36px !important;
        &:nth-child(10) {
            transform: translate(-40px, 0) !important;
        }
        &:last-child {
            transform: translate(-10px, -140px) !important;
        }
    }
    
}