.bar {
    &__arrest {
      fill: #C41F1F;
    }
    &__highlight {
        fill: #fff;
    }
}

.axis {
    font-size: 13px;
}

.axis path,
.axis line {
    fill: none;
    display: none;
}

.label {
    font-size: 13px;
}

.chart {
    padding-top: 70px;
    svg {
        background-color: #222;
    }
    // &__title {
    //     margin-bottom: 100px;
    //     padding-top: 40px;
    // }
    h2 {
        margin-top: 0;
        position: relative;
        span {
            position: absolute;
            top: 0;
            left: 0;
            &:first-child {
                position: relative;
            }
        }
    }
    &__summary {
        position: absolute;
        background-color: transparent !important;
    }
}