@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.cover {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  // @include media($media-mobile...) {
  //   padding: 20px 0 50px;
  // }
  &__text {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    pointer-events: none;
  }
  &__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background-size: cover;
    width: 100%;
    height: 100%;
    object-fit: cover;
    &--mask {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      background-size: cover;
      width: 100%;
      height: 100%;
      object-fit: cover;
      mask-image: url('./heading.svg');
      mask-repeat: no-repeat;
      mask-position: center;
      mask-size: contain;
      opacity: 0;
      @include media($media-mobile...) {
        mask-image: url('./heading_mobile.svg');
      }
      // animation: fadein 0.5s linear 0.8s forwards;
    }
  }
  &__invert {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('./heading.svg');
    background-repeat: no-repeat;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0;
    background-color: #222;
    @include media($media-mobile...) {
      background-image: url('./heading_mobile.svg');
    }
  }
  &__chart {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    padding-top: 60px;
    opacity: 0;
    padding: 70px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-between;
    background-color: rgba(0,0,0,0.3);
    svg {
      height: 100%;
      width: 70%;
      @include media($media-mobile...) {
        width: 100%;
      }
    }
    &__a, &__b {
      position: relative;
      @include media($media-mobile...) {
        display: flex;
        flex-direction: column-reverse;
      }
    }
    &__a {
      color: #6DB76D;
      height: 39.4%;
      @include media($media-mobile...) {
        margin-bottom: 25px;
      }
    }
    &__b {
      height: 56%;
    }
    @include media($media-mobile...) {
      padding: 40px 25px;
    }
  }
  &__chartmark {
    position: absolute;
    right: 0;
    text-align: right;
    @include media($media-mobile...) {
      top: auto;
      bottom: 0;
      position: relative;
    }
    &.middle {
      color: #fff;
      @include media($media-mobile...) {
        right: auto;
        left: 0;
        top: auto;
        bottom: 0;
      }
    }
    &.bottom {
      bottom: 0;
      top: auto;
      color: #C41F1F;
      @include media($media-mobile...) {
        right: 0;
        top: auto;
        bottom: 0;
      }
    }
  }
  &__charttitle {
    font-size: 20px;
    margin-right: 0.5em;
    font-weight: 700;
    @include media($media-mobile...) {
      font-size: 16px;
      display: inline-block;
    }
  }
  &__chartnumber {
    font-size: 10vw;
    font-weight: 700;
    line-height: 1;
    @include media($media-mobile...) {
      display: inline-block;
      font-size: 60px;
      font-size: 9vw;
      line-height: 1.2;
    }
  }
}

@keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}