@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.vertiscroll {
  width: 100%;
  display: flex;
  &__image {
    width: 60%;
    position: relative;
    margin-left: -70px;
    .image {
      width: 100%;
      display: block;
      position: absolute;
      top: 0;
      @include media($media-mobile...) {
        padding-bottom: 20px;
        background-color: #222;
      }
      img {
        width: 100%;
      }
    }
    @include media($media-mobile...) {
      width: 100%;
      margin-left: 0;
    }
  }
  &__text {
    font-size: 20px;
    line-height: 2;
    width: 40%;
    margin-left: 70px;
    letter-spacing: 0.1em;
    @include media($media-mobile...) {
      padding-top: 80%;
      width: 80%;
      font-size: 16px;
      margin-left: auto;
      margin-right: 0;
    }
  }
  @include media($media-mobile...) {
    display: block;
    padding-top: 0;
  }
}