@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.voicePhoto {
  // padding-left: 0 !important;
  // padding-right: 0 !important;
  button {
    left: 0;
    width: 120px;
    height: 60px;
    background-color: #C41F1F;
    border: none;
    outline: none;
    border-radius: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 15px;
    margin-top: 20px;
    color: #fff;
    z-index: 4;
    &:hover {
      cursor: pointer;
    }
    @include media($media-mobile...) {
      width: 80px;
      height: 40px;
      margin-top: 10px;
      font-size: 12px;
      padding: 0 10px;
    }
  }
  &__waveform {
    width: 40%;
    height: 100px;
    @include media($media-mobile...) {
      width: calc(100% - 80px);
      height: 60px;
    }
    wave {
      height: 100% !important;
    }
  }
  &__wrapper {
    display: flex;
    justify-content: flex-end;
    margin-top: -100px;
    @include media($media-mobile...) {
      margin-top: -60px;
    }
  }
  &__imagewrapper {
    width: calc(60% + 70px);
    margin-left: -70px;
    @include media($media-mobile...) {
      width: calc(100% + 25px);
      margin-left: -25px;
    }
  }
  &__image {
    padding-top: 66.6%;
    background-size: contain;
    background-repeat: no-repeat;
  }
}